        apiList: [], // api
        enviromentnameList: [], // 
        deployunitList: [], // 
        caseparamtypelist: [], // 
        caseparamsbytypelist: [], // 
        tmpcaseparamslist: [], // getcaseparamsbytype
        sourcetestcaseList: [],
        assertList: [],
        headerList: [], // Header
        requestHeadList: [], // Header
        listLoading: false, // 
