          add: 'API'
        },
        AsserttextMap: {
          updateRole: '',
          update: '',
          add: ''
        },
        diclevelQuery: {
          page: 1, // 
          size: 10, // 
          diccode: 'casecondition' // 
        },
        btnLoading: false, // 
        tmpapicases: {
          id: '',
          apiid: '',
